const burger = document.querySelector('.burger')
const headerNav = document.querySelector('.header__nav')
const navList = document.querySelector('.nav-list')
const navItems = document.querySelectorAll('.nav-list__item')
const overlay = document.querySelectorAll('.main')

burger.addEventListener('click', () => {
  burger.classList.toggle('open-menu')
  headerNav.classList.toggle('open-menu')
  navList.classList.toggle('open-menu')
  overlay.forEach((element) => element.classList.toggle('opacity'))
  if (burger.classList.contains('open-menu')) {
    document.body.style.overflow = 'hidden'
  } else {
    document.body.style.overflow = 'visible'
  }
})
